.cardList {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;

  .categoryName {
    font-size: 30px;
    font-weight: 600;
    margin-bottom: 32px;
  }

  .resultBox {
    width: 90%;
    margin: 80px 0 70px 0;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;

    .swiper-slide {
      display: flex;
      justify-content: center;
    }

    .swiper-button-prev {
      background: url('https://leechengwon.github.io/goobne/images/Cart/arrow-left-short.svg')
        no-repeat;
      background-size: contain;
      position: absolute;
      width: 60px;
      height: 60px;
      top: 40%;
      left: -9px;

      &::after {
        display: none;
      }
    }

    .swiper-button-next {
      background: url('https://leechengwon.github.io/goobne/images/Cart/arrow-right-short.svg')
        no-repeat;
      background-size: contain;
      position: absolute;
      width: 60px;
      height: 60px;
      top: 40%;
      right: -10px;

      &::after {
        display: none;
      }
    }

    .mainItem {
      cursor: pointer;
      display: flex;
      flex-direction: column;
      align-items: center;
      width: 220px;

      :hover {
        opacity: 0.9;
      }
      @media screen and (max-width: 1023px) {
        width: 320px;
      }
      @media screen and (max-width: 915px) {
        width: 280px;
      }
      @media screen and (max-width: 839px) {
        width: 280px;
      }
      @media screen and (max-width: 440px) {
        width: 200px;
      }

      .productImg {
        width: 100%;
        max-width: 100%;
        height: auto;
        object-fit: cover;
        aspect-ratio: 1 / 1;
        border-radius: 10px;
        :hover {
          opacity: 0.9;
        }
      }

      .cardTextBox {
        margin: 16px 0 0 !important;
        color: #333333;
        align-items: flex-start;
        justify-content: flex-start;
        :hover {
          opacity: 0.9;
        }
      }

      .productPrice {
        column-gap: 4px;
        margin-bottom: 0;
        font-weight: 700;
        margin-top: 20px;
        :hover {
          opacity: 0.9;
        }
      }

      .productName {
        display: inline-block;
        margin-top: 4px;
        font-weight: 400;
        line-height: 24px;
        height: 2.6em;
        :hover {
          opacity: 0.9;
        }
      }

      .productDesc {
        margin: 0 0 14px;
        line-height: 19px;
        font-size: 15px;
        font-weight: 400;
        margin-bottom: 4px;
        color: #999999;
        :hover {
          opacity: 0.9;
        }
      }
    }
  }
}
