.event {
  width: 100%;
  font-size: 16px;
  font-weight: 400;
  text-align: center;
  background-color: var(--header--blueA);
  color: var(--white);

  b {
    font-weight: bold;
    line-height: 50px;
  }
}

.header-Section {
  width: 100%;
  color: white;
  text-wrap: nowrap;
  position: relative;
  z-index: 1;

  .inner-wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 1200px;
    width: 100%;
    margin: 0 auto;
    padding: 0 40px;

    @media screen and (max-width: 545px) {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      width: 100%;
      padding-bottom: 10px;
    }

    .main-logo-inner {
      display: flex;
      align-items: center;
      gap: 20px;

      @media screen and (max-width: 545px) {
        display: none;
      }

      span {
        @media screen and (max-width: 865px) {
          display: none;
        }
      }
    }
  }

  header {
    display: flex;
    justify-content: space-between;
    padding: 80px 0px;
    background-color: #0177ed;

    .sing-list-btn {
      background-color: var(--header--blue);
      border: none;
      color: white;
      text-decoration-line: none;
      cursor: pointer;
      font-size: 14px;
      &:hover {
        color: white;
        opacity: 0.7;
      }
    }

    .sing-list-inner {
      display: flex;
      align-items: center;
      gap: 30px;
    }

    .mainTitle {
      font-size: 50px;
      font-weight: bold;
      color: white;
      text-decoration-line: none;
    }
  }
  .category {
    padding: 20px 0px;
    cursor: pointer;
    button {
      cursor: pointer;
    }
  }
  .viewList {
    display: flex;
    justify-content: space-between;
    top: 0px;
    align-items: center;
    background-color: #0177ed;
    padding-top: 20px;

    span {
      font-size: 20px;
      font-weight: 500;
    }

    .nav-list-inner {
      display: flex;
      align-items: center;
      width: 300px;
      justify-content: space-between;

      @media screen and (max-width: 545px) {
        justify-content: center;
        width: 100%;
      }
    }

    .categoryList {
      display: flex;
      gap: 10px;
      button {
        background-color: #0177ed;
        border: none;
        color: white;
        font-size: 18px;
        font-weight: 600;
      }
    }

    i {
      font-size: 20px;
    }
  }

  .tabWrapper {
    display: flex;
    gap: 40px;

    .mainLink {
      color: white;
      text-decoration-line: none;
      font-size: 20px;
      opacity: 0.5;
      &:hover {
        color: white;
        opacity: 1;
      }
    }
    span {
      opacity: 0.5;
      &:hover {
        color: white;
        opacity: 1;
      }
    }

    @media screen and (max-width: 800px) {
      display: none;
    }
  }

  .inputI {
    display: flex;
    align-items: center;

    gap: 20px;
    .glasses {
      position: relative;

      i {
        color: black;
        position: absolute;
        bottom: 30%;
        right: 5%;
      }
    }
    input {
      width: 300px;
      border-radius: 5px;
      font-size: 18px;
      padding: 10px 10px 10px 10px;
    }
  }
  .basket {
    cursor: pointer;

    &:hover {
      color: white;
      opacity: 0.7;
    }

    @media screen and (max-width: 374px) {
      display: none;
    }
  }
}
