.details {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-wrap: nowrap;
  margin-bottom: 200px;
  margin-top: 100px;

  .container {
    display: flex;
    justify-content: center;
    max-width: 1200px;
    gap: 60px;
    margin-top: 40px;
    padding: 0 10px;

    @media screen and (max-width: 1178px) {
      flex-direction: column;
      align-items: center;
    }

    .imgBox {
      width: 500px;
      height: 500px;

      img {
        width: 100%;
        height: 100%;
        border-radius: 10px;
        aspect-ratio: 1/1;
      }
      @media screen and (max-width: 600px) {
        width: 380px;
        height: 380px;
      }

      @media screen and (max-width: 425px) {
        width: 320px;
        height: 320px;
      }
    }

    .decision {
      padding: 0 10px;

      .title {
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 15px;
        margin: 30px 0px;

        h1 {
          font-size: 30px;
          font-weight: 800;
        }

        i {
          font-size: 24px;
        }

        @media screen and (max-width: 425px) {
          h1 {
            font-size: 24px;
          }
        }
      }

      .brandName {
        font-size: 18px;
        font-weight: 800;

        @media screen and (max-width: 768px) {
          padding-left: 35px;
        }

        @media screen and (max-width: 425px) {
          padding-left: 0;
        }
      }
    }

    .assistant {
      width: 100%;
      font-size: 18px;
      color: #999999;

      @media screen and (max-width: 768px) {
        text-align: center;
      }

      @media screen and (max-width: 570px) {
        font-size: 14px;
        white-space: pre-line;
      }
    }

    .counterWrap {
      display: flex;
      justify-content: space-between;
      align-items: center;

      @media screen and (max-width: 425px) {
      }
    }

    .price {
      font-size: 40px;
      margin: 30px 0px;
      display: block;

      @media screen and (max-width: 768px) {
        padding-left: 35px;
      }

      @media screen and (max-width: 425px) {
        font-size: 24px;
        padding: 0;
        margin: 0;
      }
    }

    .totalPriceBox {
      display: flex;
      align-items: center;
      justify-content: flex-end;
      padding-right: 30px;
      gap: 10px;

      .totalPrice {
        font-size: 24px;
      }
    }

    .totalPriceBox {
      display: flex;
      align-items: center;
      justify-content: flex-end;
      gap: 10px;

      @media screen and (max-width: 425px) {
        span:first-child {
          font-size: 16px;
        }
      }

      .totalPrice {
        font-size: 30px;
        font-weight: bold;

        @media screen and (max-width: 425px) {
          font-size: 28px;
        }
      }
    }
  }

  .basket {
    width: 100%;
    padding: 20px;
    background-color: black;
    color: white;
    font-size: 20px;
    border: none;
    cursor: pointer;
    border-radius: 4px;

    &:hover {
      background-color: #999999;
    }

    @media screen and (max-width: 425px) {
      padding: 10px;
    }
  }
}
