.countAll {
  display: flex;
  justify-content: space-between;
  vertical-align: baseline;
  margin: 30px;

  p {
    font-size: 20px;
    font-weight: 500;
  }

  .countBox {
    display: flex;
    align-items: center;
    gap: 20px;
    border: 1px solid #999999;

    button {
      border: none;
      width: 32px;
      height: 32px;
      font-size: 20px;
      color: black;

      &:first-child {
        border-right: 1px solid #999999;
      }

      &:last-child {
        border-left: 1px solid #999999;
      }

      @media screen and (max-width: 425px) {
        width: 24px;
        height: 24px;
        font-size: 16px;
      }
    }
  }
}
