.product {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;

  h2 {
    font-size: 50px;
    text-align: center;
    font-weight: 700;
    margin-top: 100px;
    margin-bottom: 100px;

    @media screen and (max-width: 400px) {
      font-size: 30px;
      font-weight: 700;
    }
  }
  .goodsContainer {
    width: 70%;
  }
  .total {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
    .select {
      padding: 0px 10px;
      color: black;
      option {
        color: black;
      }
    }
    p {
      font-size: 20px;
      font-weight: 600;

      @media screen and (max-width: 400px) {
        font-size: 14px;
        font-weight: 700;
      }
    }
  }
  .pagination {
    width: 100%;
    display: flex;
    justify-content: center;
    gap: 20px;
    margin: 40px 0px 20px 0px;

    span {
      font-size: 25px;
      cursor: pointer;
    }
    .clicked {
      font-weight: 800;
      border-bottom: 3px solid black;
      padding: 0px 2px;
    }
    .notClicked:hover {
      color: gray;
      opacity: 0.7;
    }
    .arrow:hover {
      color: gray;
      opacity: 0.7;
    }
  }
}
