.popup-Wrap {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 100;

  .popup {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    position: absolute;
    border: 1px solid black;
    border-radius: 10px;
    width: 400px;
    height: 200px;
    position: absolute;
    z-index: 100;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    @media screen and (max-width: 550px) {
      width: 100%;
    }

    .delete {
      width: 90%;
      display: flex;
      justify-content: end;
      color: gray;
      font-size: 20px;
      cursor: pointer;

      button {
        border: none;
        padding: 5px 5px;
        border-radius: 4px;
        background-color: black;
        color: white;
      }
      &:hover {
        color: black;
        opacity: 0.8;
      }
    }
    .explanation {
      display: flex;
      justify-content: center;
      text-align: center;
      font-size: 20px;
      margin: 20px 0px;
      width: 100%;

      @media screen and (max-width: 550px) {
        font-size: 16px;
      }
    }
    .choice {
      display: flex;
      justify-content: center;
      text-align: center;
      width: 100%;
      gap: 30px;

      button {
        padding: 8px 10px;
        border: 1px solid black;
        border-radius: 4px;
        background-color: black;
        color: white;
        font-weight: 400;
        &:hover {
          opacity: 0.8;
          cursor: pointer;
        }
      }
    }

    background-color: white;
  }
}
