.cartMain {
  width: 100%;
  height: 100%;
}
.container {
  width: 100%;
  height: 100%;
  position: relative;
  margin: 0 auto;
}
.btSection {
  height: 100%;
  width: 100%;
  max-width: 970px;
  margin: 0px auto;
  float: none;
  padding: 0px 16px;
  // border: 3px solid black;

  @media screen and (max-width: 425px) {
    padding: 0 5px;
  }
}
.btwrap {
  width: 100%;
  text-align: center;
  h2 {
    padding: 52px 0 2px;
    font-weight: 600;
    font-size: 40px;
    line-height: 140%;
    text-align: center;
  }
}
.btlist {
  height: 100%;
  border-top: 0;
  border-bottom: none;
  font-size: 16px;
  color: #9a9a9a;
  padding: 14px 0 64px;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
}
.checkboxWrap {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.checkboxInner {
  width: calc(100% - 316px - 42px);
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  border-bottom: 1px solid #1c1c1c;
  padding-bottom: 20px;
  .emptyCheckBox {
    width: 27px;
    height: 27px;
    background-size: 27px;
    cursor: pointer;
  }
  .allChoice {
    padding: 0px 16px 0px 8px;
    vertical-align: middle;
    color: #1c1c1c;
    cursor: pointer;
  }
  .checkboxInner > div {
    color: #1c1c1c;
  }
  div::before {
    content: '|';
    margin-right: 20px;
    color: #dddddd;
  }
}
.basketContainer {
  width: 100%;
  display: flex;
  flex-direction: column;

  .itemLine {
    width: 100%;
    display: flex;
    justify-content: center;
    padding: 20px;
    border-bottom: 2px solid #f9f9f9;

    @media screen and (max-width: 425px) {
      padding: 5px;
    }

    .IndividualCheckBox {
      width: 27px;
      height: 27px;
      background-size: 27px;
      margin: 0 14px 0 0;
      cursor: pointer;
    }

    .beItemWrap {
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 100%;

      .bsItemTitleWrap {
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 5px;

        @media screen and (max-width: 575px) {
          flex-direction: column;
          align-items: center;
          justify-content: center;
          gap: 5px;
        }

        .bsItemImage {
          width: 60px;
          height: 60px;
          aspect-ratio: 1/1;
          border: 1px solid black;
          border-radius: 15px;

          @media screen and (max-width: 425px) {
            margin: 0;
          }
        }

        .bsItemTitle {
          height: 100%;
          display: block;
          margin: 0 0 3px;
          font-weight: normal;
          color: #1c1c1c;
          padding-top: 5px;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
          flex: 1;

          @media screen and (max-width: 425px) {
            width: 100px;
            margin: 0;
            font-size: 12px;
          }
        }
      }

      .bsCountBox {
        display: flex;
        justify-content: center;
        border-radius: 10px;
        padding: 3px 3px;
        margin: 0px;

        .bsUp {
          color: #1c1c1c;
          font-weight: 700;
        }

        button {
          background-color: white;
          border: none;
          cursor: pointer;

          &:hover {
            color: black;
            opacity: 0.5;
          }

          @media screen and (max-width: 550px) {
            width: 24px;
            height: 24px;
          }
        }
      }

      .bsItemPrice {
        display: flex;
        justify-content: center;
        font-weight: 700;
        font-size: 18px;
        padding: 0px 20px;
        color: #1c1c1c;

        @media screen and (max-width: 550px) {
          font-size: 15px;
          padding: 0;
          width: 100%;
        }
      }

      .bsXimage {
        width: 16px;
        height: 16px;
        margin: 0;

        &:hover {
          color: black;
          opacity: 0.5;
        }
      }
    }
  }
}
.btlist .checkboxWrap > div {
  width: 100%;
  padding-bottom: 20px;
  display: flex;
  flex-direction: column;
}

.infoWrap {
  width: 100%;
  padding-bottom: 20px;
  display: flex;
  align-items: center;

  @media screen and (max-width: 970px) {
    flex-direction: column;
  }
}

p.messageWed {
  display: flex;
  width: 100%;
  padding: 140px 0;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  color: gray;
  font-size: 20px;
}
