.iconBox {
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  row-gap: 30px;
  gap: 20px;
  margin: 60px auto 80px;
  justify-content: center;

  .iconItem {
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: 9px;
    font-size: 15px;
    cursor: pointer;
    width: 100px;
    height: 100px;

    & img {
      max-width: 100px;

      @media screen and (max-width: 375px) {
        width: 60px;
        height: 60px;
      }
    }
  }
}
