@import '../../../styles/variables.scss';

.ftMain {
  width: 100%;
  background-color: var(--footer--gray);

  .logFtBox {
    max-width: 1050px;
    display: flex;
    align-items: center;
    flex-direction: row;
    justify-content: space-between;
    margin: 0 auto;
    padding: 20px 0px;

    @media screen and (max-width: 425px) {
      flex-direction: column;
      align-items: center;
    }

    .ftText {
      flex-direction: column;
      align-items: flex-start;
      font-size: 14px;
      line-height: 160%;
      padding: 0 10px;

      color: var(--gray);

      @media screen and (max-width: 425px) {
        text-align: left;

        .store {
          text-align: center;
        }
      }

      .ftTop {
        font-size: 18px;
        font-weight: 700;
        margin-bottom: 20px;
        color: var(--black);
      }

      .ftMid {
        margin-bottom: 30px;

        .fontBlack {
          font-weight: 600;
          color: var(--black);
        }
      }
    }
    .ftCs {
      width: 100%;
      max-width: 144px;

      .ftCsBtn {
        width: 100%;
        padding: 15px 0;
        border: 1px solid var(--black);
        border-radius: 5px;
        background-color: transparent;

        @media screen and (max-width: 425px) {
          margin-top: 30px;
        }
      }
    }
  }
}
